.form {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, 50%);
    display: flex;
    flex-direction: column;
    margin: 0 auto;
}

.form__title {
    font-size: 24px;
    font-weight: 600;
    margin-top: 0;
    margin-bottom: 30px;
    text-align: center;
}

.form__btn {
    width: 300px;
}